"use client";

import React, { useState, useEffect, useCallback } from "react";
import { useNavigate } from 'react-router-dom';
import { motion, AnimatePresence } from "framer-motion";
import { Timer } from 'lucide-react';
import Lottie from 'lottie-react';
import startSticker from './stickers/buster.json'
import endSticker from './stickers/buster.json'
import { createClient } from '@supabase/supabase-js';

const supabaseUrl = 'https://kjzpfvjyalipijwoesoh.supabase.co';
const supabaseKey = 'eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJpc3MiOiJzdXBhYmFzZSIsInJlZiI6ImtqenBmdmp5YWxpcGlqd29lc29oIiwicm9sZSI6ImFub24iLCJpYXQiOjE3Mjg4MzU3ODMsImV4cCI6MjA0NDQxMTc4M30.JaIDZ6LDvHFa4jptS1BfBTzjMOcAu7ig1PqTHWgqJEg';
const supabase = createClient(supabaseUrl, supabaseKey);

export default function Component() {
  const [particles, setParticles] = useState([])
  const [score, setScore] = useState(0)
  const [time, setTime] = useState(60)
  const [isGameActive, setIsGameActive] = useState(false)
  const [isGameStarted, setIsGameStarted] = useState(false)
  const [lastClickPos, setLastClickPos] = useState(null)
  const [difficulty, setDifficulty] = useState(1)
  const [damageEffect, setDamageEffect] = useState(false)
  const [userId, setUserId] = useState(null)

  const navigate = useNavigate();

  const handleHomeClick = () => {
    navigate('/game');
  };

  const handleBombClick = (particle) => {
    if (particle.isBomb) {
      navigator.vibrate([100]);

      setDamageEffect(true);
  
      setParticles((prevParticles) =>
        prevParticles.map((p) =>
          p.id === particle.id
            ? { ...p, collected: true } 
            : p
        )
      );
  

      setTimeout(() => setDamageEffect(false), 500);
    }
  };

  const boundaries = {
    left: 10,
    right: 90,
    top: 10,
    bottom: 90,
  };

  useEffect(() => {
    if (window.Telegram && window.Telegram.WebApp) {
      const telegramUser = window.Telegram.WebApp.initDataUnsafe.user
      if (telegramUser && telegramUser.id) {
        setUserId(telegramUser.id)
      }
    }
  }, [])

  useEffect(() => {
    if (!isGameActive) return;

    let animationFrameId;
    let lastTime = 0;

    const animate = (currentTime) => {
      if (lastTime !== 0) {
        const deltaTime = (currentTime - lastTime) / 1000; 
        setParticles((prev) => {
          return prev.map((particle) => ({
            ...particle,
            y: particle.y + (100 * difficulty * deltaTime), 
            rotation: particle.rotation + (180 * deltaTime), 
          })).filter((particle) => particle.y < boundaries.bottom); 
        });
      }
      lastTime = currentTime;
      animationFrameId = requestAnimationFrame(animate);
    };

    animationFrameId = requestAnimationFrame(animate);

    return () => {
      cancelAnimationFrame(animationFrameId);
    };
  }, [isGameActive, difficulty, boundaries.bottom]);

  const handleClick = useCallback(
    (event) => {
      if (!isGameActive) return;
  
      const rect = event.currentTarget.getBoundingClientRect();
      const clientX = event.touches ? event.touches[0].clientX : event.clientX;
      const clientY = event.touches ? event.touches[0].clientY : event.clientY;
  
      const x = ((clientX - rect.left) / rect.width) * 100;
      const y = ((clientY - rect.top) / rect.height) * 100;
  
      if (x < boundaries.left || x > boundaries.right || y < boundaries.top || y > boundaries.bottom) {
        return;
      }
  
      setLastClickPos({ x: clientX - rect.left, y: clientY - rect.top });
      setTimeout(() => setLastClickPos(null), 100);
  
      let closestParticle = null;
      let minDistance = Infinity;
  
      particles.forEach((particle) => {
        if (!particle.collected) {
          const distance = Math.sqrt(
            Math.pow(particle.x - x, 2) + Math.pow(particle.y - y, 2)
          );
          if (distance < 10 && distance < minDistance) {
            closestParticle = particle;
            minDistance = distance;
          }
        }
      });
  
      if (closestParticle) {
        setParticles((prev) =>
          prev.map((particle) =>
            particle.id === closestParticle.id
              ? { ...particle, collected: true }
              : particle
          )
        );
        if (closestParticle.isBomb) {
          const penalty = Math.floor(100 * 500) - 500;
          setScore((s) => Math.max(0, s - penalty));
          setDamageEffect(true);
          setTimeout(() => setDamageEffect(false), 200);
  
          if (navigator.vibrate) {
            navigator.vibrate(200); 
          } else {
            console.log("Vibration API is not supported");
          }
        } else {
          const points = Math.floor(Math.random() * 151) + 100;
          setScore((s) => s + points);
        }
      }
    },
    [isGameActive, particles, boundaries.left, boundaries.right, boundaries.top, boundaries.bottom]  // Add boundaries values
  );
  

  useEffect(() => {
    if (!isGameActive) return;

    const interval = setInterval(() => {
      setTime((prev) => {
        if (prev <= 0) {
          setIsGameActive(false);
          return 0;
        }
        return prev - 1;
      });

      if (time % 10 === 0) {
        setDifficulty((prev) => Math.min(prev + 0.2, 3));
      }
    }, 1000);

    return () => clearInterval(interval);
  }, [isGameActive, time]);

  useEffect(() => {
    if (!isGameActive) return;

    let normalParticleCount = 0;

    const interval = setInterval(() => {
      setParticles((prev) => {
        normalParticleCount++;
        const isBomb = normalParticleCount % 5 === 0; 
        if (isBomb) normalParticleCount = 0;

        const newParticle = {
          id: Date.now(),
          x: Math.random() * (boundaries.right - boundaries.left) + boundaries.left,
          y: boundaries.top,
          collected: false,
          rotation: Math.random() * 360,
          scale: isBomb ? 1 : (Math.random() * 0.5 + 0.5),
          color: isBomb ? 'rgb(255, 0, 0)' : `hsl(${Math.random() * 360}, 100%, 50%)`,
          isBomb: isBomb,
        };
        return [...prev, newParticle];
      });
    }, 1000 / difficulty);

    return () => clearInterval(interval);
  }, [isGameActive, difficulty]);

  const startGame = () => {
    setIsGameStarted(true);
    setIsGameActive(true);
    setScore(0);
    setTime(60);
    setParticles([]);
    setDifficulty(1);
  };

  const updateUserBalance = async (score) => {
    if (!userId) {
      console.error('User ID is not available')
      return
    }

    try {
      const { data, error } = await supabase
        .from('profiles')
        .select('balance')
        .eq('user_id', userId)
        .single()

      if (error) throw error

      const newBalance = (data.balance || 0) + score

      const { error: updateError } = await supabase
        .from('profiles')
        .update({ balance: newBalance })
        .eq('user_id', userId)

      if (updateError) throw updateError

      console.log('Balance updated successfully')
    } catch (error) {
      console.error('Error updating balance:', error)
    }
  }

  useEffect(() => {
    if (!isGameActive && isGameStarted && time === 0) {
      updateUserBalance(score)
    }
  }, [isGameActive, isGameStarted, time, score, userId, updateUserBalance])

  return (
    <div className="relative w-full h-screen bg-black overflow-hidden">
      <div
        className="absolute inset-0 opacity-20"
        style={{
          backgroundImage:
            "linear-gradient(#1a1a1a 1px, transparent 1px), linear-gradient(90deg, #1a1a1a 1px, transparent 1px)",
          backgroundSize: "40px 40px",
        }}
      />

          {damageEffect && (
            <div className="absolute inset-0 bg-red-500 opacity-30 z-50 pointer-events-none animate-colorChange" />
          )}

      <div
        className="relative w-full h-full touch-none"
        onMouseDown={handleClick}
        onTouchStart={handleClick}
      >
        <div
          className="absolute"
          style={{
            left: `${boundaries.left}%`,
            right: `${100 - boundaries.right}%`,
            top: `${boundaries.top}%`,
            bottom: `${100 - boundaries.bottom}%`,
          }}
        />

{isGameStarted && (
          <div className="absolute top-4 left-4 right-4 flex justify-between items-center">
            <div className="flex items-center bg-black/70 backdrop-blur-sm rounded-full px-4 py-2">
              <Timer className="w-6 h-6 mr-2 text-green-400" />
              <span className="text-green-400 font-mono text-2xl font-bold">
                {time.toString().padStart(2, "0")}
              </span>
            </div>
            <div className="bg-black/70 backdrop-blur-sm rounded-full px-4 py-2">
              <span className="text-green-400 font-mono text-2xl font-bold">P:{score}</span>
            </div>
          </div>
        )}

        <AnimatePresence>
          {particles.map((particle) => (
            <motion.div
              key={particle.id}
              initial={{ opacity: 1, scale: 1}}
              animate={{ opacity: particle.collected ? 0 : 1, scale: particle.collected ? 0 : 1 }}
              exit={{ scale: 0, opacity: 0 }}
              transition={{ duration: 0.2 }}
              className={`absolute w-8 h-8 ${particle.isBomb ? 'animate-pulse' : ''}`}
              style={{
                left: `${particle.x}%`,
                top: `${particle.y}%`,
                transform: `rotate(${particle.rotation}deg) scale(${particle.scale})`,
              }}
              onClick={() => handleBombClick(particle)}
            >
              <svg
                viewBox="0 0 24 24"
                className="w-full h-full"
                style={{ color: particle.color }}
              >
                {particle.isBomb ? (
                  <>
                    <circle cx="12" cy="12" r="10" fill="currentColor" />
                    <circle cx="12" cy="12" r="6" fill="black" />
                    <path d="M15 9l-6 6M9 9l6 6" stroke="white" strokeWidth="2" />
                  </>
                ) : (
                  <path
                    fill="currentColor"
                    d="M12 0l3 8l8 3l-8 3l-3 8l-3-8l-8-3l8-3z"
                  />
                )}
              </svg>
            </motion.div>
          ))}
        </AnimatePresence>

        <AnimatePresence>
          {lastClickPos && (
            <motion.div
              initial={{ scale: 0.5, opacity: 1 }}
              animate={{ scale: 2, opacity: 0 }}
              exit={{ opacity: 0 }}
              transition={{ duration: 0.2 }}
              className="absolute w-8 h-8 border-2 border-green-400 rounded-full"
              style={{
                left: lastClickPos.x,
                top: lastClickPos.y,
                transform: "translate(-50%, -50%)",
              }}
            />
          )}
        </AnimatePresence>

        {!isGameStarted && (
          <div className="absolute inset-0 flex flex-col items-center justify-center">
            <div className="w-64 h-64 mb-8">
              <Lottie
                animationData={startSticker}
                loop
                autoplay
              />
            </div>
            <button
              onClick={startGame}
              className="px-6 py-3 bg-[#eb2dab8a] text-white text-lg font-bold rounded-full hover:bg-[#eb2dab8a] transition-colors"
            >
              Начать
            </button>
          </div>
        )}

        {isGameStarted && !isGameActive && (
          <div className="absolute inset-0 bg-black/80 flex flex-col items-center justify-center">
            <div className="w-64 h-64 mb-8">
              <Lottie
                animationData={endSticker}
                loop
                autoplay
              />
            </div>
            <h2 className="text-2xl text-green-400 mb-4">Игра окончена</h2>
            <p className="text-green-400 mb-4">Собрано: {score}</p>
            <button
              onClick={handleHomeClick}
              className="px-4 py-2 bg-[#f525259c] text-white rounded-full hover:bg-[#f525259c] transition-colors"
            >
              Пока
            </button>
          </div>
        )}
      </div>
    </div>
  );
}