'use client'

import { useState, useEffect } from 'react'
import { motion, AnimatePresence } from 'framer-motion'
import { useNavigate } from 'react-router-dom';
import { HomeIcon, Gift, Coins, Star } from 'lucide-react';
import Lottie from 'lottie-react'
import { createClient } from '@supabase/supabase-js'; 
import duckAnimation from './stickers/box.json'
import openingboxing from './stickers/openbox.json'
const supabaseUrl = 'https://kjzpfvjyalipijwoesoh.supabase.co';
const supabaseKey = 'eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJpc3MiOiJzdXBhYmFzZSIsInJlZiI6ImtqenBmdmp5YWxpcGlqd29lc29oIiwicm9sZSI6ImFub24iLCJpYXQiOjE3Mjg4MzU3ODMsImV4cCI6MjA0NDQxMTc4M30.JaIDZ6LDvHFa4jptS1BfBTzjMOcAu7ig1PqTHWgqJEg';
const supabase = createClient(supabaseUrl, supabaseKey);

export default function Component() {
  const [isOpening, setIsOpening] = useState(false)
  const [showReward, setShowReward] = useState(false)
  const [openedBox, setOpenedBox] = useState(null)
  const [energy, setEnergy] = useState(0); 
  const navigate = useNavigate();

  useEffect(() => {
    const tg = window.Telegram.WebApp;
  
    const user = tg.initDataUnsafe?.user;
    if (user && user.id) {
      const userId = user.id.toString();
  
      const fetchUserData = async () => {
        const { data, error } = await supabase
          .from('profiles')
          .select('energy')
          .eq('user_id', userId)
          .single();
  
        if (error) {
          console.error(error);
        } else {
          setEnergy(data.energy); // Используем энергию
        }
      };
  
      fetchUserData();
    } else {
      console.error("User ID is not available.");
    }
  }, []);

  const handlerBoxClick = () => {
    navigate('/box');
  };

  const handleHomeClick = () => {
    navigate('/game');
  };

  const handleRatingClick = () => {
    navigate('/rating');
  };

  const handleBoxClick = (index) => {
    if (!isOpening && energy > 0) { 
      setIsOpening(true);
      setOpenedBox(index);
      setTimeout(() => {
        setShowReward(true);
      }, 1000);
      setEnergy(prevEnergy => prevEnergy - 1);
    }
  };


  const stickers = [
    { animationData: duckAnimation, label: "Стикер 1" },
    { animationData: duckAnimation, label: "Стикер 2" },
  ];


  const handleClaimReward = async () => {

    setShowReward(false);
    setIsOpening(false);
    setOpenedBox(null);
  };


  return (
    <div className="min-h-screen bg-black text-white flex flex-col items-center justify-center p-6">
      {/* Отображение энергии пользователя */}

      <motion.div
              className="absolute top-10 left-0 right-0 text-center text-lg text-gray-300"
              initial={{ opacity: 0 }}
              animate={{ opacity: 1 }}
              transition={{ delay: 0.5 }}
            >
              <span>Попытки: {energy}</span>
            </motion.div>

      <AnimatePresence>
        {!showReward ? (
          <motion.div
            className="relative w-96 h-96 flex flex-wrap justify-center gap-4"
            initial={{ scale: 1 }}
            animate={{ scale: 1 }}
            exit={{ scale: 0, opacity: 0 }}
          >
            {/* Карточка с коробками */}
            {[...Array(12)].map((_, index) => (
              <motion.div
                key={index}
                className="w-20 h-20 bg-transparent flex items-center justify-center cursor-pointer rounded-lg"
                onClick={() => handleBoxClick(index)}
                initial={{ opacity: 1 }}
                animate={{ opacity: openedBox === index ? 0 : 1 }}
                transition={{ duration: 0.3 }}
              >
                {/* Отображение стикеров в коробке */}
                <Lottie
                  animationData={stickers[index % stickers.length].animationData} // Используем стикеры по кругу
                  loop
                  autoplay
                  className="w-full h-full object-contain"
                />
              </motion.div>
            ))}
            {/* Информационный текст снизу */}
            <motion.div
              className="w-full text-center text-lg text-gray-300 mt-4"
              initial={{ opacity: 0 }}
              animate={{ opacity: 1 }}
              transition={{ delay: 0.5 }}
            >
              <span>Нажмите на любого из крика для получения награды</span>
            </motion.div>
          </motion.div>
        ) : (
          <motion.div
            className="relative w-full max-w-md aspect-square flex flex-col items-center justify-center"
            initial={{ scale: 0 }}
            animate={{ scale: 1 }}
            transition={{ type: 'spring', damping: 12 }}
          >
            <div className="relative z-10 flex flex-col items-center">
              <motion.h1
                className="text-4xl font-bold mb-8"
                initial={{ y: -50, opacity: 0 }}
                animate={{ y: 0, opacity: 1 }}
                transition={{ delay: 0.2 }}
              >
                Поздравляем!
              </motion.h1>

              <motion.div
                className="w-32 h-32 mb-8"
                initial={{ scale: 0 }}
                animate={{ scale: 1 }}
                transition={{ delay: 0.4, type: 'spring', damping: 8 }}
              >
                <Lottie
                  animationData={openingboxing}
                  loop
                  autoplay
                  className="w-full h-full"
                />
              </motion.div>

              <motion.div
                className="flex items-center gap-2 text-2xl font-bold"
                initial={{ y: 50, opacity: 0 }}
                animate={{ y: 0, opacity: 1 }}
                transition={{ delay: 0.6 }}
              >
                <span>ВЫ ВЫИГРАЛИ</span>
                <div className="flex items-center bg-[#1a1a1a] px-3 py-1 rounded-full">
                  <Coins className="w-6 h-6 text-emerald-500 mr-2" />
                  <span className="text-green-400">0</span>
                </div>
              </motion.div>

              <motion.button
                className="mt-8 px-6 py-3 bg-gradient-to-r from-purple-600 to-blue-600 rounded-full font-bold text-lg shadow-lg hover:from-purple-700 hover:to-blue-700 transition-all"
                initial={{ y: 50, opacity: 0 }}
                animate={{ y: 0, opacity: 1 }}
                transition={{ delay: 0.8 }}
                onClick={handleClaimReward}
              >
                БОЛЬШИЕ ВОЗНАГРАЖДЕНИЯ
              </motion.button>
            </div>
          </motion.div>
        )}
      </AnimatePresence>
      <div className="fixed bottom-0 left-0 right-0 flex justify-around bg-black/50 backdrop-blur-lg p-4">
        <button onClick={handleHomeClick} className="flex flex-col items-center bg-transparent border-none text-white">
          <HomeIcon className="h-6 w-6" />
          <span className="text-xs mt-1">Домой</span>
        </button>
        <button onClick={handlerBoxClick} className="flex flex-col items-center">
          <Gift className="h-6 w-6 text-white" />
          <span className="text-xs mt-1 text-white">Подарок</span>
        </button>
        <button onClick={handleRatingClick} className="flex flex-col items-center">
          <Star className="h-6 w-6 text-white" />
          <span className="text-xs mt-1 text-white">Рейтинг</span>
        </button>
      </div>
    </div>
  );
}
