'use client'

import React, { useState, useEffect } from 'react';
import { createClient } from '@supabase/supabase-js';
import { HomeIcon, Gift, Star, Trophy, Check } from 'lucide-react';
import { useNavigate } from 'react-router-dom';

const supabaseUrl = 'https://kjzpfvjyalipijwoesoh.supabase.co';
const supabaseKey = 'eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJpc3MiOiJzdXBhYmFzZSIsInJlZiI6ImtqenBmdmp5YWxpcGlqd29lc29oIiwicm9sZSI6ImFub24iLCJpYXQiOjE3Mjg4MzU3ODMsImV4cCI6MjA0NDQxMTc4M30.JaIDZ6LDvHFa4jptS1BfBTzjMOcAu7ig1PqTHWgqJEg';
const supabase = createClient(supabaseUrl, supabaseKey);

function UserCard({ user, rank, isCurrentUser = false }) {
  return (
    <div className={`bg-[#1c1c1e] rounded-xl p-4 mb-2 ${isCurrentUser ? 'border border-yellow-400' : ''}`}>
      <div className="flex items-center justify-between">
        <div className="flex items-center gap-4">
          <div className="relative">
            <div className="w-12 h-12 rounded-full bg-blue-500 overflow-hidden">
              <img
                src="/placeholder.svg"
                alt={user.nickname}
                width={48}
                height={48}
                className="w-full h-full object-cover"
              />
            </div>
           
          </div>
          <div>
            <div className="flex items-center gap-2">
              <span className="font-medium">{user.nickname}</span>
             
            </div>
            <div className="flex items-center gap-2 text-sm">
              <span className="text-yellow-400">★</span>
              <span>{user.balance}</span>
              <span className="text-emerald-400 ml-2">₮</span>
            </div>
          </div>
        </div>
        <div className="text-gray-400">#{rank}</div>
      </div>
    </div>
  );
}

export default function LeaderBoard() {
  const [currentUser, setCurrentUser] = useState(null);
  const [leaderboardData, setLeaderboardData] = useState([]);
  const navigate = useNavigate();

  const handleBoxClick = () => navigate('/box')
  const handleHomeClick = () => navigate('/game')
  const handleRatingClick = () => navigate('/rating')

  useEffect(() => {
    const fetchData = async () => {
      try {
        if (window.Telegram?.WebApp) {
          const tg = window.Telegram.WebApp;
          const userId = tg.initDataUnsafe?.user?.id;

          const { data: users, error } = await supabase
            .from('profiles')
            .select('user_id, nickname, balance')
            .order('balance', { ascending: false })
            .limit(20);

          if (error) throw error;

          const { data: userData } = await supabase
            .from('profiles')
            .select('user_id, nickname, balance')
            .eq('user_id', userId)
            .single();

          setCurrentUser(userData);
          setLeaderboardData(users || []);
        }
      } catch (error) {
        console.error('Error fetching data:', error);
      }
    };

    fetchData();
  }, []);

  return (
    <div className="min-h-screen bg-black text-white">
      {/* Current User Card */}
      {currentUser && (
        <div className="p-4">
          <div className="bg-[#1c1c1e] rounded-xl p-4">
            <div className="flex items-center justify-between">
              <div className="flex items-center gap-2">
                <span className="text-sm">.visuals meta</span>
                <span className="bg-yellow-400 text-black text-xs px-2 py-0.5 rounded">You</span>
              </div>
              <div className="text-sm text-gray-400">#{currentUser.rank || '0'}</div>
            </div>
            <div className="flex items-center gap-2 mt-1">
              <span className="text-yellow-400">★</span>
              <span>750</span>
            </div>
          </div>
        </div>
      )}

      {/* Top 100 Header */}
      <div className="px-4 py-2">
        <h2 className="text-xl font-bold">Top 100</h2>
      </div>

      {/* Leaderboard List */}
      <div className="p-4">
        {leaderboardData.map((user, index) => (
          <UserCard
            key={user.user_id}
            user={user}
            rank={index + 4}
          />
        ))}
      </div>


      {/* Fixed Navigation Bar */}
      <div className="flex justify-around bg-black/50 backdrop-blur-lg p-4">
        <button onClick={handleHomeClick} className="flex flex-col items-center bg-transparent border-none text-white">
          <HomeIcon className="h-6 w-6" />
          <span className="text-xs mt-1">Домой</span>
        </button>
        <button onClick={handleBoxClick} className="flex flex-col items-center">
          <Gift className="h-6 w-6 text-white" />
          <span className="text-xs mt-1 text-white">Подарок</span>
        </button>
        <button onClick={handleRatingClick} className="flex flex-col items-center">
          <Star className="h-6 w-6 text-white" />
          <span className="text-xs mt-1 text-white">Рейтинг</span>
        </button>
      </div>
    </div>
  );
}