import React, { useEffect } from 'react';

export const LoadingScreen = ({ onLoadingComplete }) => {
  useEffect(() => {
    const timer = setTimeout(() => {
      onLoadingComplete();
    }, 950);
    return () => clearTimeout(timer);
  }, [onLoadingComplete]);

  return (
    <div className="fixed inset-0 bg-black flex flex-col items-center justify-center">
      <h2 className="text-white text-2xl mb-4 sr-only">Loading</h2>
      <div 
        className="spinner-border animate-spin inline-block w-16 h-16 border-4 border-white border-t-transparent rounded-full"
        aria-label="Loading"
      ></div>
    </div>
  );
};