'use client'

import { createClient } from '@supabase/supabase-js'
import React, { useState, useRef, useEffect } from 'react'
import { useNavigate } from 'react-router-dom'
import { motion, AnimatePresence } from 'framer-motion'
import { HomeIcon, Gift, Star } from 'lucide-react'
import Lottie from 'lottie-react'

import LeagueProgress from './scripts/LeagueProgress'
import FloatingNumber from './scripts/FloatingNumber'

import { LoadingScreen } from './LoadingScreen'

import stickerAnimation from './stickers/stick.json'
import userSticker from './stickers/name.json'
import floatingButtonSticker from './stickers/buster.json'
import BoosterStick from './stickers/boost.json'

const supabaseUrl = 'https://kjzpfvjyalipijwoesoh.supabase.co'
const supabaseKey = 'eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJpc3MiOiJzdXBhYmFzZSIsInJlZiI6ImtqenBmdmp5YWxpcGlqd29lc29oIiwicm9sZSI6ImFub24iLCJpYXQiOjE3Mjg4MzU3ODMsImV4cCI6MjA0NDQxMTc4M30.JaIDZ6LDvHFa4jptS1BfBTzjMOcAu7ig1PqTHWgqJEg'
const supabase = createClient(supabaseUrl, supabaseKey)

const getLeague = (balance) => {
  if (balance < 5000) return 'Бронзовая'
  if (balance < 15000) return 'Серебряная'
  if (balance < 50000) return 'Золотая'
  if (balance < 500000) return 'Платиновая'
  return 'Бриллиантовая'
}

export default function GameInterface() {
  const [balance, setBalance] = useState(0)
  const [isLoading, setIsLoading] = useState(true)
  const [nickname, setNickname] = useState('')
  const [floatingNumbers, setFloatingNumbers] = useState([])
  const [league, setLeague] = useState('')
  const [userBoost, setUserBoost] = useState(1)
  const userIdRef = useRef(null)
  const navigate = useNavigate()
  const lastY = useRef(0)
  const lastShakeTime = useRef(0)
  const [config, setConfig] = useState(null)

  const handleBoxClick = () => navigate('/box')
  const handleHomeClick = () => navigate('/game')
  const handleRatingClick = () => navigate('/rating')
  const handleEnergyClick = () => navigate('/energy')
  const handleShopPage = () => navigate('/ShopPage')

  const updateBalance = async (newBalance) => {
    if (!userIdRef.current) {
      console.error('User ID is not available')
      return
    }

    try {
      const { error: updateError } = await supabase
        .from('profiles')
        .update({ balance: newBalance })
        .eq('user_id', userIdRef.current)

      if (updateError) {
        console.error('Ошибка обновления баланса:', updateError)
      }
    } catch (err) {
      console.error('Ошибка соединения с Supabase:', err)
    }
  }

  const addFloatingNumber = (value) => {
    const id = Date.now()
    const x = Math.random() * window.innerWidth
    setFloatingNumbers(prev => [...prev, { id, x, value }])
    setTimeout(() => {
      setFloatingNumbers(prev => prev.filter(num => num.id !== id))
    }, 2000)
  }

  useEffect(() => {
    const init = async () => {
      try {
        if (typeof window !== 'undefined' && window.Telegram && window.Telegram.WebApp) {
          const tg = window.Telegram.WebApp

          const user = tg.initDataUnsafe?.user
          if (user && user.id) {
            const userId = user.id.toString()
            const userFirstName = user.first_name
            setNickname(userFirstName)
            userIdRef.current = userId

            const { data: userData, error: fetchError } = await supabase
              .from('profiles')
              .select('balance, boost')
              .eq('user_id', userId)
              .single()

            if (fetchError) {
              console.error('Ошибка получения данных пользователя:', fetchError)
            } else if (userData) {
              setBalance(userData.balance || 0)
              setUserBoost(userData.boost || 1)
            }
          } else {
            console.error('User data is not available')
          }
        } else {
          console.error('Telegram WebApp is not available')
        }
      } catch (err) {
        console.error('Ошибка инициализации приложения:', err)
      } finally {
        setIsLoading(false)
      }
    }

    init()
  }, [])

  useEffect(() => {
    const fetchConfig = async () => {
      const { data, error } = await supabase
        .from('config')
        .select('*')
        .single()
      
      if (error) {
        console.error('Error fetching config:', error)
      } else {
        setConfig(data)
      }
    }

    fetchConfig()
  }, [])

  useEffect(() => {
    setLeague(getLeague(balance))
  }, [balance])

  useEffect(() => {
    if (!config) return

    const { shakeThreshold, cooldownPeriod, updateInterval } = config

    if (window.Telegram?.WebApp) {
      const accelerometer = window.Telegram.WebApp.Accelerometer
      const WebApp = window.Telegram.WebApp

      WebApp.expand()
      WebApp.isVerticalSwipesEnabled = false

      accelerometer.start({ refresh_rate: 60 }, (success) => {
        if (!success) {
          console.error('Failed to start accelerometer tracking.')
        }
      })

      const interval = setInterval(() => {
        const y = accelerometer.y
        const currentTime = Date.now()
        const timeSinceLastShake = currentTime - lastShakeTime.current

        const movement = Math.abs(y - lastY.current)

        if (movement > shakeThreshold && timeSinceLastShake > cooldownPeriod) {
          setBalance(prevBalance => {
            const newBalance = prevBalance + userBoost
            updateBalance(newBalance)
            addFloatingNumber(userBoost)
            for (let i = 0; i < 3; i++) {
              setTimeout(() => {
                WebApp.HapticFeedback.impactOccurred('medium')
              }, i * 100)
            }
            return newBalance
          })
          lastShakeTime.current = currentTime
        }

        lastY.current = y
      }, updateInterval)

      return () => {
        clearInterval(interval)
        accelerometer.stop()
      }
    } else {
      console.error('Accelerometer API is not supported.')
    }
  }, [config, userBoost])

  if (isLoading) {
    return <LoadingScreen onLoadingComplete={() => setIsLoading(false)} />
  }

  return (
    <div className="min-h-screen bg-black">
      <AnimatePresence mode="wait">
        <div
          className="interaction-zone min-h-screen bg-black text-white flex flex-col items-center justify-center overflow-hidden relative"
          style={{
            minHeight: 'calc(var(--vh, 1vh) * 100)',
            touchAction: 'none',
          }}
        >
          <div className="absolute top-4 left-4 flex flex-col">
            <div className="flex items-center">
              <div className="w-10 h-10 mr-2">
                <Lottie
                  animationData={userSticker}
                  loop
                  autoplay
                  className="w-full h-full"
                />
              </div>
              <span className="text-white text-lg font-semibold">
                {nickname}
              </span>
            </div>
            <LeagueProgress balance={balance} />
          </div>

          <motion.div
            className="fixed right-10 top-1/3 -translate-y-1/3 flex flex-col items-center"
            animate={{
              y: [-5, 5],
            }}
            transition={{
              y: {
                repeat: Infinity,
                repeatType: "reverse",
                duration: 1.5,
                ease: "easeInOut"
              }
            }}
          >
            <div className="w-16 h-16 rounded-xl overflow-hidden bg-[#ffffff20] bg-opacity-50 relative">
              <Lottie
                animationData={floatingButtonSticker}
                loop
                autoplay
                onClick={handleEnergyClick}
                className="w-full h-full absolute top-0 left-0"
              />
            </div>
            <span className="text-xs text-white">GAME</span>
          </motion.div>

          <motion.div
            className="fixed left-10 top-1/3 -translate-y-1/3 flex flex-col items-center"
            animate={{
              y: [-5, 5],
            }}
            transition={{
              y: {
                repeat: Infinity,
                repeatType: "reverse",
                duration: 1.5,
                ease: "easeInOut"
              }
            }}
          >
            <div className="w-16 h-16 rounded-xl overflow-hidden bg-[#ffffff20] bg-opacity-50 relative">
              <Lottie
                animationData={BoosterStick}
                loop
                autoplay
                onClick={handleShopPage}
                className="w-full h-full absolute top-0 left-0"
              />
            </div>
            <span className="text-xs text-white">SHOP</span>
          </motion.div>

          <div className="text-5xl font-bold mb-4 flex items-center gap-2">
            {balance.toLocaleString()}
          </div>

          <div className="text-2xl font-semibold mb-32">
            Лига: {league}
          </div>

          <motion.div
            className="sticker-interaction relative"
            animate={{
              rotate: [0, 5, -5, 0],
              transition: {
                repeat: Infinity,
                duration: 2,
              },
            }}
          >
            <div className="w-64 h-64 flex items-center justify-center">
              <Lottie
                animationData={stickerAnimation}
                loop
                autoplay
                className="w-full h-full absolute"
              />
            </div>
          </motion.div>

          <AnimatePresence>
            {floatingNumbers.map(({ id, x, value }) => (
              <FloatingNumber key={id} x={x} value={value} />
            ))}
          </AnimatePresence>

          <div className="fixed bottom-0 left-0 right-0 flex justify-around bg-black/50 backdrop-blur-lg p-4">
            <button onClick={handleHomeClick} className="flex flex-col items-center bg-transparent border-none text-white">
              <HomeIcon className="h-6 w-6" />
              <span className="text-xs mt-1">Домой</span>
            </button>
            <button onClick={handleBoxClick} className="flex flex-col items-center">
              <Gift className="h-6 w-6 text-white" />
              <span className="text-xs mt-1 text-white">Подарок</span>
            </button>
            <button onClick={handleRatingClick} className="flex flex-col items-center">
              <Star className="h-6 w-6 text-white" />
              <span className="text-xs mt-1 text-white">Рейтинг</span>
            </button>
          </div>
        </div>
      </AnimatePresence>
    </div>
  )
}
