import React, { useState, useEffect } from 'react';
import { createClient } from '@supabase/supabase-js'
import { useNavigate } from 'react-router-dom';
import { HomeIcon, Gift, CircleDollarSign, ArrowLeft, Star } from 'lucide-react';
import AnimatedSticker from './scripts/AnimatedSticker';
import './Shop.css';

// Import sticker data
import whiteSticker from './stickers/shop.json';
import blackSticker from './stickers/shop.json';
import brownSticker from './stickers/shop.json';
import purpleSticker from './stickers/shop.json';
import redSticker from './stickers/shop.json';
import blueSticker from './stickers/shop.json';
import greenSticker from './stickers/shop.json';
import yellowSticker from './stickers/shop.json';

const supabaseUrl = 'https://kjzpfvjyalipijwoesoh.supabase.co'
const supabaseKey = 'eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJpc3MiOiJzdXBhYmFzZSIsInJlZiI6ImtqenBmdmp5YWxpcGlqd29lc29oIiwicm9sZSI6ImFub24iLCJpYXQiOjE3Mjg4MzU3ODMsImV4cCI6MjA0NDQxMTc4M30.JaIDZ6LDvHFa4jptS1BfBTzjMOcAu7ig1PqTHWgqJEg'
const supabase = createClient(supabaseUrl, supabaseKey)

const ShopItem = ({ color, speed, price, stickerData, onPurchase }) => (
  <div className="bg-blue-600 rounded-2xl p-4 flex flex-col items-center">
    <div className="w-32 h-32 mb-4">
      <AnimatedSticker stickerData={stickerData} width="100%" height="100%" />
    </div>
    <div className="text-white">
      <div className="mb-2">BOOST +{speed}</div>
      <button 
        className="bg-blue-500 w-full py-2 px-4 rounded-lg flex items-center justify-center gap-2"
        onClick={() => onPurchase(color, speed, price)}
      >
        {price} <CircleDollarSign className="w-4 h-4 fill-current" /> VSC
      </button>
    </div>
  </div>
);

const ShopPage = () => {
  const navigate = useNavigate();
  const [userBalance, setUserBalance] = useState(0);
  const [userBoost, setUserBoost] = useState(0);
  const [message, setMessage] = useState('');
  const [userId, setUserId] = useState(null);
  const [shopItems, setShopItems] = useState([
    { color: 'White', speed: 1, price: 1000, stickerData: whiteSticker },
    { color: 'Black', speed: 2, price: 3000, stickerData: blackSticker },
    { color: 'Brown', speed: 3, price: 8000, stickerData: brownSticker },
    { color: 'Purple', speed: 4, price: 13000, stickerData: purpleSticker },
    { color: 'Red', speed: 5, price: 20000, stickerData: redSticker },
    { color: 'Blue', speed: 6, price: 27000, stickerData: blueSticker },
    { color: 'Green', speed: 7, price: 35000, stickerData: greenSticker },
    { color: 'Yellow', speed: 8, price: 40000, stickerData: yellowSticker },
  ]);

  useEffect(() => {
    const telegramUserId = window.Telegram?.WebApp?.initDataUnsafe?.user?.id;
    if (telegramUserId) {
      setUserId(telegramUserId);
      fetchUserData(telegramUserId);
    }
  }, []);

  const fetchUserData = async (userId) => {
    try {
      const { data, error } = await supabase
        .from('profiles')
        .select('balance, boost')
        .eq('user_id', userId)
        .single();

      if (error) throw error;
      setUserBalance(data.balance);
      setUserBoost(data.boost);
    } catch (error) {
      console.error('Error fetching user data:', error);
      setMessage('Ошибка при получении пользовательских данных. Пожалуйста, попробуйте еще раз.');
    }
  };

  const handlePurchase = async (color, speed, price) => {
    if (!userId) {
      setMessage('Пользователь не прошел аутентификацию. Пожалуйста, войдите в систему.');
      return;
    }

    if (userBalance < price) {
      setMessage('Недостаточно средств для совершения покупки.');
      return;
    }

    try {
      const { data, error } = await supabase.rpc('purchase_boost', {
        p_user_id: userId,
        p_boost: speed,
        p_price: price
      });

      if (error) throw error;

      if (data.success) {
        setUserBalance(data.new_balance);
        setUserBoost(data.new_boost);
        setMessage(`Успешно приобретенный BOOST +${speed}!`);

        // Update the price of the purchased item
        setShopItems(prevItems => 
          prevItems.map(item => 
            item.color === color 
              ? { ...item, price: item.price * 2 } 
              : item
          )
        );
      } else {
        setMessage(data.message || 'Покупка не удалась. Пожалуйста, попробуйте еще раз.');
      }
    } catch (error) {
      console.error('Error processing purchase:', error);
      setMessage('При обработке вашей покупки произошла ошибка.');
    }
  };

  return (
    <div className="min-h-screen bg-black flex flex-col">
      {/* Header - Fixed */}
      <div className="fixed top-0 left-0 right-0 bg-black/50 backdrop-blur-lg z-20 p-4">
        <div className="flex justify-between items-center">
          <button onClick={() => navigate(-1)} className="text-white flex items-center gap-2">
            <ArrowLeft className="w-6 h-6" />
          </button>
          <div className="flex items-center gap-4">
            <div className="text-white">{userBalance} VSC</div>
            <div className="text-white">BOOST: {userBoost}</div>
          </div>
        </div>
      </div>

      {/* Main Content - Scrollable */}
      <div className="flex-1 overflow-y-auto pt-20 pb-24">
        <div className="p-4">
          <h1 className="text-4xl font-bold text-white mb-2">Низкий поклон</h1>
          <h2 className="text-4xl font-bold text-white mb-4">вам в нашем магазе</h2>
          <p className="text-gray-400 mb-8">
            Каждый скин уникальный и даёт вам различные преимущества для роста банана 🍌
          </p>

          {message && (
            <div className="bg-blue-500 text-white p-2 rounded mb-4">
              {message}
            </div>
          )}

          <div className="grid grid-cols-2 gap-4">
            {shopItems.map((item, index) => (
              <ShopItem key={index} {...item} onPurchase={handlePurchase} />
            ))}
          </div>
        </div>
      </div>

      {/* Bottom Navigation - Fixed */}
      <div className="fixed bottom-0 left-0 right-0 bg-black/50 backdrop-blur-lg z-20 p-4">
        <div className="flex justify-around">
          <button
            onClick={() => navigate('/game')}
            className="flex flex-col items-center text-white"
          >
            <HomeIcon className="h-6 w-6" />
            <span className="text-xs mt-1">Домой</span>
          </button>
          <button
            onClick={() => navigate('/box')}
            className="flex flex-col items-center text-white"
          >
            <Gift className="h-6 w-6" />
            <span className="text-xs mt-1">Подарок</span>
          </button>
          <button
            onClick={() => navigate('/rating')}
            className="flex flex-col items-center text-white"
          >
            <Star className="h-6 w-6" />
            <span className="text-xs mt-1">Рейтинг</span>
          </button>
        </div>
      </div>
    </div>
  );
};

export default ShopPage;

