const LeagueProgress = ({ balance }) => {
    const getLeagueThresholds = (balance) => {
      if (balance < 5000) return { current: 0, next: 5000 }
      if (balance < 15000) return { current: 5000, next: 15000 }
      if (balance < 50000) return { current: 15000, next: 50000 }
      if (balance < 500000) return { current: 50000, next: 500000 }
      return { current: 500000, next: 500000 }
    }
  
    const { current, next } = getLeagueThresholds(balance)
    const progress = next === current ? 100 : ((balance - current) / (next - current)) * 100
  
    return (
      <div className="w-32 h-1 bg-gray-700 rounded-full overflow-hidden">
        <div 
          className="h-full bg-green-500 transition-all duration-300 ease-out"
          style={{ width: `${progress}%` }}
        />
      </div>
    )
  }
  
  export default LeagueProgress
  