import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { createClient } from '@supabase/supabase-js';
import { LoadingScreen } from './LoadingScreen';

const supabaseUrl = 'https://kjzpfvjyalipijwoesoh.supabase.co';
const supabaseKey = 'eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJpc3MiOiJzdXBhYmFzZSIsInJlZiI6ImtqenBmdmp5YWxpcGlqd29lc29oIiwicm9sZSI6ImFub24iLCJpYXQiOjE3Mjg4MzU3ODMsImV4cCI6MjA0NDQxMTc4M30.JaIDZ6LDvHFa4jptS1BfBTzjMOcAu7ig1PqTHWgqJEg';
const supabase = createClient(supabaseUrl, supabaseKey);

export default function WelcomePage() {
  const [isChecking, setIsChecking] = useState(true);
  const [progress, setProgress] = useState(0)
  const [showText, setShowText] = useState(false)
  const navigate = useNavigate();

  useEffect(() => {
    const timer = setInterval(() => {
      setProgress((oldProgress) => {
        if (oldProgress === 100) {
          clearInterval(timer)
          setTimeout(() => setShowText(true), 200)
          return 100
        }
        return Math.min(oldProgress + 1, 100)
      })
    }, 50)

    return () => {
      clearInterval(timer)
    }
  }, [])


  useEffect(() => {
    const checkAndCreateUser = async () => {
      try {
        if (typeof window !== 'undefined' && window.Telegram && window.Telegram.WebApp) {
          const tg = window.Telegram.WebApp;

          tg.expand();
          tg.enableClosingConfirmation();

          const userAgent = navigator.userAgent.toLowerCase();
          if (!userAgent.includes("mobile")) {
            navigate('/OnlyMobile');
            return; 
          }

          const userId = tg.initDataUnsafe?.user?.id;
          const userFirstName = tg.initDataUnsafe?.user?.first_name;

          if (userId) {
            const { data: user, error } = await supabase
              .from('profiles')
              .select('balance')
              .eq('user_id', userId)
              .single();

            if (error && error.code === 'PGRST116') {
              const { error: insertError } = await supabase
                .from('profiles')
                .insert([{ user_id: userId, balance: 1, nickname: userFirstName }]);
              
              if (insertError) {
                console.error('Ошибка создания пользователя:', insertError);
              } else {
                console.log('Новый пользователь создан');
              }
            } else if (user) {
              console.log('Пользователь найден');
            }
          }
        }
      } catch (err) {
        console.error('Ошибка проверки/создания пользователя:', err);
      } finally {
        setIsChecking(false);
      }
    };

    checkAndCreateUser();

    const timer = setTimeout(() => {
      navigate('/game');
    }, 100);

    return () => clearTimeout(timer);
  }, [navigate]);

  if (isChecking) {
    return <LoadingScreen onLoadingComplete={() => {}} />;
  }

  return (
    <div className="flex items-center justify-center min-h-screen bg-black">
      <div className="w-64 text-center">
        <div className="mb-4">
          <div className="h-2 bg-gray-700 rounded-full">
            <div
              className="h-full bg-[#ffffff] rounded-full transition-all duration-500 ease-out"
              style={{ width: `${progress}%` }}
            ></div>
          </div>
        </div>
        <div className="relative h-8">
          <p
            className={`text-[#ffffff] font-bold absolute inset-0 transition-opacity duration-500 ${
              showText ? 'opacity-100' : 'opacity-0'
            }`}
          >
            Created by Visuals Community
          </p>
        </div>
      </div>
    </div>
  )
}